import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CsatService } from '../csat.service';

declare const require: (path: string) => any;

@Component({
  selector: 'app-csat',
  templateUrl: './csat.component.html',
  styleUrls: ['./csat.component.css'],
})
export class CsatComponent implements OnInit {
  surveys = [
    {
      question: 'Are you happy with the waiting time ?',
      options: ['Very Unhappy', 'Unhappy', 'Neutral', 'Happy', 'Very Happy'],
      faces: true,
    },
    {
      question: 'Are you happy with the service provided by our Grab staff ?',
      options: ['Very Unhappy', 'Unhappy', 'Neutral', 'Happy', 'Very Happy'],
      faces: true,
    },
    {
      question: 'How easy was it to complete your transaction ?',
      options: ['Very Difficult', 'Difficult', 'Not Easy', 'Easy', 'Very Easy'],
      faces: true,
    },
    {
      question:
        'How likely are you to recommend Grab to your family and friends ?',
      options: ['Very Unlikely', 'Unlikely', 'Maybe', 'Likely', 'Very Likely'],
      faces: true,
    },
    {
      question: 'How can we improve to serve you better ?',
      options: [
        'Online Information',
        'Self-Help',
        'More Branches',
        'Waiting Time',
        'Partner Benefits',
      ],
      faces: false,
    },
  ];
  page;
  survey;
  answers;

  constructor(
    public csatService: CsatService,
    protected route: ActivatedRoute,
    protected router: Router
  ) {}

  ngOnInit() {
    this.init();
  }

  init() {
    this.page = undefined;
    this.survey = 0;
    this.answers = this.surveys.map(s => '');
  }

  assets(image) {
    return require(`./assets/${image}`);
  }

  go(page) {
    this.router.navigate(['..', page], { relativeTo: this.route });
  }

  nextPage() {
    if (this.page === undefined) {
      this.page = 'page1';
      return;
    }
    if (this.page === 'page1') {
      this.page = 'survey';
      return;
    }
    if (this.page === 'survey') {
      this.survey++;
      if (this.survey === this.surveys.length) {
        this.page = 'thanks';
      }
      return;
    }
  }
}
